<template>
  <div v-if="match !== null" :id="`match-${match.time}`">
    <GridMatch v-bind="{match}"/>
    <h3 class="mt-5">Map Veto</h3>
    <div class="container mb-3 d-none d-xl-flex justify-content-center" v-if="match.status === 'finished'">
      <div class="row">
        <div class="col-auto px-0">
          <Map
              v-for="p in [...match.mappool.bans.a.slice(0,1), ...match.mappool.picks.a, ...match.mappool.bans.a.slice(1)]"
              class="col-auto" :key="p.name" v-bind="p"/>
        </div>
        <div class="col-auto px-0">
          <Map class="col-auto" style="margin-top: calc(6em + 10px);" :key="match.mappool.decider.name"
               v-bind="match.mappool.decider"/>
        </div>
        <div class="col-auto px-0">
          <Map
              v-for="p in [...match.mappool.bans.b.slice(0,1), ...match.mappool.picks.b, ...match.mappool.bans.b.slice(1)]"
              class="col-auto" :key="p.name" v-bind="p"/>
        </div>
      </div>
    </div>
    <div class="container mb-3 d-xl-none" v-if="match.status === 'finished'">
      <Map
          v-for="p in [...match.mappool.mobilebans.slice(0,2), ...match.mappool.picks.a, ...match.mappool.picks.b, ...match.mappool.mobilebans.slice(2), ...match.mappool.decider]"
          class="col-auto" :key="p.name" v-bind="p"/>
    </div>
    <h3 class="mt-5">Статистика матча</h3>
    <div class="btn-group btn-group-sm" role="group" v-if="match.status === 'finished'">
      <button type="button" class="btn btn-sm btn-dark" v-for="map in match.maps"
              :class="selectedScoreboard !== null && selectedScoreboard.map === map.map ? 'active' : ''"
              :key="`mapbtn-${map.map}`"
              @click.prevent="switchMap(map)">
        {{ officialMapPool[map.map].name }}
      </button>
    </div>
    <Scoreboard v-if="selectedScoreboard !== null" v-bind="{match, scoreboard: selectedScoreboard}"/>
  </div>
</template>

<script>
import {matchStatuses, matchTitles} from "@/constants/InternalStorage";
import GridMatch from "@/components/molecules/GridMatch";
import MapPool from "@/constants/MapPool";
import Map from "@/components/molecules/Map";
import Scoreboard from "@/components/molecules/Scoreboard";

export default {
  name: "MatchPanel",
  components: {GridMatch, Map, Scoreboard},
  data: () => ({
    match: null,
    matchStatuses,
    matchTitles,
    officialMapPool: MapPool,
    selectedScoreboard: null
  }),
  created() {
    this.updateMatches(this.$store.state.matches)
    this.unwatch = this.$store.watch((state, getters) => getters.matches, (matches) => {
      this.updateMatches(matches)
    })
  },
  methods: {
    updateMatches(matches) {
      if (this.$route.params.id) {
        this.match = matches.find(it => it.id === this.$route.params.id) ?? null
        if (this.match !== null && this.match.status === "finished" && this.selectedScoreboard === null)
          this.switchMap(this.match.maps[0])
      }
    },
    switchMap(map) {
      this.selectedScoreboard = map;
    }
  },
  deactivated() {
    this.$destroy();
  },
  beforeDestroy() {
    this.unwatch();
  }
}
</script>