<template>
    <div class="mappool-map" :style="{'background-image': `url(\'${image}\')`}" :class="{'mappool-ban': ban}">
        <h5 :style="{'background': `linear-gradient(to right, ${convertHex(textbg, 0)}, ${convertHex(textbg, 100)} 40%, ${convertHex(textbg, 100)} 60%, ${convertHex(textbg, 0)})`}">
            {{ name }}
            <span class="sub-h5" v-if="ban">Бан ({{ ban }})</span>
            <span class="sub-h5" v-if="pick">Пик ({{ pick }})</span>
            <span class="sub-h5" v-if="decider">Решающая</span>
        </h5>
    </div>
</template>

<script>
    export default {
        name: "Map",
        props: ['image', 'name', 'textbg', 'ban', 'pick', 'decider'],
        methods: {
            convertHex: function (color, opacity) {
                color = color.replace('#', '')
                const r = parseInt(color.substring(0, 2), 16)
                const g = parseInt(color.substring(2, 4), 16)
                const b = parseInt(color.substring(4, 6), 16)
                return 'rgba(' + r + ',' + g + ',' + b + ',' + opacity / 100 + ')'
            }
        }
    }
</script>
