<template>
  <div class="scoreboard" v-if="scoreboard !== null && match !== null">
    <div class="scoreboard-score mb-3">
      <div class="col">{{ match.teams.a.name }}</div>
      <div class="col-5 col-md-3 matchscore">
                <span :class="scoreboard.score.a > scoreboard.score.b ? 'matchscore_more' : 'matchscore_less'">
                    {{ scoreboard.score.a }}
                </span>
        &nbsp;&mdash;&nbsp;
        <span :class="scoreboard.score.b > scoreboard.score.a ? 'matchscore_more' : 'matchscore_less'">
                    {{ scoreboard.score.b }}
                </span>
      </div>
      <div class="col">{{ match.teams.b.name }}</div>
    </div>
    <div class="scoreboard-table table-responsive" v-for="team in ['a','b']" :key="`scoreboard-team${team}`">
      <table class="table table-hover table-main">
        <thead>
        <tr class="text-center">
          <th class="w-50">{{ match.teams[team].name }}</th>
          <th title="Убийств">K</th>
          <th title="Смертей">D</th>
          <th title="Помощи">A</th>
          <th title="Помощь световой">FA</th>
          <th title="Установлено бомб">BP</th>
          <th title="Разминировано бомб">BD</th>
          <th title="Трейды">TK</th>
          <th title="Убийств союзников">TMK</th>
          <th title="Убийств-Смертей" @click.prevent="kd_divide=!kd_divide">KD</th>
          <th title="Средний урон за раунд">ADR</th>
          <th title="Фрагов за раунд">FRP</th>
          <th title="Процент хедшотов">HSP</th>
          <th title="Эйсы">ACES</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="player in scoreboard.scoreboard[team]" :key="player.steamid" class="text-center">
          <td class="text-left"><a :href="`//steamcommunity.com/profiles/${player.steamid}`">{{
              player.info ? player.info.nickname : player.steamid
            }}</a></td>
          <td>{{ player.kills }}</td>
          <td>{{ player.deaths }}</td>
          <td>{{ player.assists }}</td>
          <td>{{ player.flashes }}</td>
          <td>{{ player.bombplants }}</td>
          <td>{{ player.bombdefuses }}</td>
          <td>{{ player.trades }}</td>
          <td>{{ player.teamkills }}</td>
          <td :class="player.kills - player.deaths >= 0 ? 'text-light-success' : 'text-light-danger'">
            {{ getKD(player.kills, player.deaths) }}
          </td>
          <td>{{ (player.damage / player.rounds).toPrecision(3) }}</td>
          <td>{{ (player.kills / player.rounds).toPrecision(player.kills / player.rounds >= 1 ? 3 : 2) }}</td>
          <td>{{ player.headshots === 0 ? 0 : (player.headshots / player.kills * 100).toPrecision(3) }}%</td>
          <td>{{ player["5kr"] }}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="container">
      <p v-if="scoreboard.demo"><a target="_blank" :href="scoreboard.demo">Официальная запись игры (демо)</a></p>
      <i class="text-secondary">Чтобы узнать что означает каждая колонка статистики, наведите на неё курсором</i><br>
      <i class="text-secondary">Для смены формулы подсчёта KD, нажмите на название колонки</i>
    </div>
  </div>
</template>

<script>
export default {
  name: "Scoreboard",
  props: ['scoreboard', 'match'],
  data: () => ({
    kd_divide: false
  }),
  methods: {
    getKD(k, d) {
      if (this.kd_divide)
        return (k / d).toPrecision(k / d >= 1 ? 3 : 2)
      else
        return (k - d >= 0 ? "+" : "") + (k - d)
    }
  }
}
</script>