import Vue from 'vue'
import App from './App.vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'

import {getCommonData} from "@/constants/InternalStorage";
import {updateCommonData} from "@/processors/DataProcessor";
import {routes, routeTitleChanger} from "@/constants/Router";

import '@/assets/styles.scss'

import jQuery from "jquery"
import ('bootstrap/dist/js/bootstrap.bundle.min')

Vue.config.productionTip = false

window.jQuery = window.$ = jQuery

Vue.use(Vuex)
Vue.use(VueRouter)

const router = new VueRouter({routes})
router.afterEach((to) => routeTitleChanger(Vue, to))

const store = new Vuex.Store({
    state: {
        teams: [],
        players: [],
        matches: [],
        rating: {common: {}, group_stage: {}},
        player_rating: []
    },
    getters: {
        teams: state => state.teams,
        players: state => state.players,
        matches: state => state.matches,
        rating: state => state.rating,
        player_rating: state => state.player_rating
    },
    mutations: {
        updateCommonData: (state, commonData) => updateCommonData(state, commonData)
    },
    actions: {
        async autoUpdate(context) {
            const commonData = await getCommonData()
            context.commit('updateCommonData', commonData)
        }
    }
})

new Vue({
    render: h => h(App),
    router, store
}).$mount('#app')
