<template>
    <div id="teams">
        <GridTeam v-for="team in teams" :key="team.id" v-bind="team"/>
    </div>
</template>

<script>
    import GridTeam from "@/components/molecules/GridTeam";

    export default {
        name: "PlayersPanel",
        data: () => ({
            teams: []
        }),
        components: {GridTeam},
        created() {
            this.updateTeams(this.$store.state.teams)
            this.unwatch = this.$store.watch((state, getters) => getters.teams, (teams) => {
                this.updateTeams(teams)
            })
        },
        methods: {
            updateTeams(teams) {
                this.teams = [...teams.filter(it => !it.tech), ...teams.filter(it => it.tech)]
            }
        },
        beforeDestroy() {
            this.unwatch();
        }
    }
</script>

