<template>
  <div id="rating">
    <h3 @click="slideDiv('commonrating')" class="cursor-pointer no-select">Общий рейтинг
      <SlidingIcon :state="states.commonrating"/>
    </h3>
    <div class="table-responsive mb-3" id="commonrating">
      <table class="table table-main table-hover">
        <RatingTableHead type="Общий рейтинг"/>
        <tbody>
        <GridRating v-for="rating in commonRating" :key="rating.ratingKey" v-bind:rating="rating"/>
        </tbody>
      </table>
    </div>
    <h3 @click="slideDiv('playoff')" class="cursor-pointer no-select">Плейофф
      <SlidingIcon :state="states.playoff"/>
    </h3>
    <div id="playoff" class="container overflow-auto">
      <bracket :rounds="playoff" v-if="playoff.length > 0">
        <template #player="{ player }">
          {{ player.name }}
        </template>
        <template #player-extension-bottom="{ match }">
          <div @click.prevent="match.match?goMatch(match.match):false" class="tomatch" :class="match.match ? 'cursor-pointer' : 'cursor-default'">
            {{ match.match ? "Перейти к матчу" : "Матч недоступен" }}
          </div>
        </template>
      </bracket>
    </div>
    <h3 @click="slideDiv('grouprating')" class="cursor-pointer no-select">Групповой этап
      <SlidingIcon :state="states.grouprating"/>
    </h3>
    <div class="row" id="grouprating">
      <div class="col-md-6">
        <div class="table-responsive">
          <table class="table table-main table-hover">
            <RatingTableHead type="Группа A"/>
            <tbody>
            <GridRating v-for="rating in groupstage.a" :key="rating.ratingKey" v-bind:rating="rating"/>
            </tbody>
          </table>
        </div>
      </div>
      <div class="col-md-6">
        <div class="table-responsive">
          <table class="table table-main table-hover">
            <RatingTableHead type="Группа C"/>
            <tbody>
            <GridRating v-for="rating in groupstage.c" :key="rating.ratingKey" v-bind:rating="rating"/>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GridRating from "@/components/molecules/GridRating";
import SlidingIcon from "@/components/atoms/SlidingIcon";
import RatingTableHead from "@/components/molecules/RatingTableHead";
import Bracket from "vue-tournament-bracket";

export default {
  name: "RatingPanel",
  data: () => ({
    commonRating: {},
    groupstage: {a: {}, c: {}},
    states: {commonrating: false, grouprating: false, playoff: false},
    playoff: [],
    matches: []
  }),
  components: {RatingTableHead, SlidingIcon, GridRating, Bracket},
  created() {
    this.updateRatings(this.$store.state.rating)
    this.unwatch_ratings = this.$store.watch((state, getters) => getters.rating, (rating) => {
      this.updateRatings(rating)
    })

    this.updatePlayoffMatches(this.$store.state.matches)
    this.unwatch_matches = this.$store.watch((state, getters) => getters.matches, (matches) => {
      this.updatePlayoffMatches(matches)
    })
  },
  methods: {
    updateRatings(rating) {
      this.commonRating = Object.values(rating.common)
      this.groupstage.a = Object.values(rating.group_stage).filter(it => it.team.group === "A")
      this.groupstage.c = Object.values(rating.group_stage).filter(it => it.team.group === "C")
    },
    updatePlayoffMatches(matches) {
      this.matches = matches.filter(it => it.title === 'playoff');
      const semifinals = this.matches.filter(it => it.id.startsWith("semifinals"));
      const final = this.matches.filter(it => it.id.startsWith("finals"))
      if (semifinals.length === 0)
        return
      this.playoff = [{
        games: [{
          player1: {...semifinals[0].teams.a, winner: semifinals[0].score.a > semifinals[0].score.b},
          player2: {...semifinals[0].teams.b, winner: semifinals[0].score.a < semifinals[0].score.b},
          match: semifinals[0].id
        }, {
          player1: {...semifinals[1].teams.a, winner: semifinals[1].score.a > semifinals[1].score.b},
          player2: {...semifinals[1].teams.b, winner: semifinals[1].score.a < semifinals[1].score.b},
          match: semifinals[1].id
        }]
      }, {
        games: final.length > 0 ? [{
          player1: {...final[0].teams.a, winner: final[0].score.a > final[0].score.b},
          player2: {...final[0].teams.b, winner: final[0].score.a < final[0].score.b},
          match: final[0].id
        }] : [{
          player1: {name: 'TBA', winner: false},
          player2: {name: 'TBA', winner: false}
        }]
      }]
    },
    slideDiv(id) {
      window.$(`#${id}`).slideToggle(500)
      this.states[id] = !this.states[id]
    },
    goMatch(matchId) {
      this.$router.push(`/match/${matchId}`)
    }
  },
  beforeDestroy() {
    this.unwatch_ratings();
    this.unwatch_matches();
  }
}
</script>

