<template>
  <div id="players">
    <div class="d-flex mb-2 align-items-center">
      <label for="teamSelect" class="text-nowrap m-0 mr-3">Выбор команды</label>
      <select class="custom-select bg-dark border-dark text-light flex-grow-1" id="teamSelect" v-model="selectedTeam">
        <option selected :value="null">Все команды</option>
        <option v-for="team in teams" :key="'selteam'+team.id" :value="team.id">{{ team.name }}</option>
      </select>
    </div>
    <div class="table-responsive">
      <table class="table table-main">
        <thead>
        <tr class="text-center">
          <th scope="col">Игрок</th>
          <th title="Убийств">K</th>
          <th title="Смертей">D</th>
          <th title="Помощи">A</th>
          <th title="Помощь световой">FA</th>
          <th title="Установлено бомб">BP</th>
          <th title="Разминировано бомб">BD</th>
          <th title="Трейды">TK</th>
          <th title="Убийств союзников">TMK</th>
          <th title="Убийств-Смертей" @click.prevent="kd_divide=!kd_divide">KD</th>
          <th title="Средний урон за раунд">ADR</th>
          <th title="Фрагов за раунд">FRP</th>
          <th title="Процент хедшотов">HSP</th>
          <th title="Эйсы">ACES</th>
        </tr>
        </thead>
        <tbody>
        <GridPlayer v-for="player in filteredRating" :key="player.steamid" :kd_divide="kd_divide" v-bind="player"/>
        </tbody>
      </table>
      <p class="text-secondary">Итого: {{ filteredRating.length }}</p>
    </div>
  </div>
</template>

<script>
import GridPlayer from "@/components/molecules/GridPlayer";

export default {
  name: "PlayersPanel",
  data: () => ({
    rating: [],
    teams: [],
    selectedTeam: null,
    kd_divide: false
  }),
  components: {GridPlayer},
  created() {
    this.rating = this.$store.state.player_rating
    this.teams = this.$store.state.teams
    this.unwatch_player_rating = this.$store.watch((state, getters) => getters.player_rating, (rating) => {
      this.rating = rating
    });
    this.unwatch_teams = this.$store.watch((state, getters) => getters.teams, (teams) => {
      this.teams = teams
    });
  },
  computed: {
    filteredRating() {
      if (!this.selectedTeam)
        return this.rating
      return this.rating.filter(it => it.info.team === this.selectedTeam)
    }
  },
  beforeDestroy() {
    this.unwatch_player_rating();
    this.unwatch_teams();
  }
}
</script>

