<template>
  <div class="mb-4 teamgrid">
    <h3>{{ name }}</h3>
    <div class="players">
      <div v-for="p in players" class="player" :key="p.steamid">
        <a :href="`//steamcommunity.com/profiles/${p.steamid}`">
          <div class="avatar"><img :src="p.playerImg" class="img-fluid" alt=""></div>
          {{ p.nickname }}
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "GridTeam",
  props: ['name', 'players']
}
</script>