      </div>
    </td>
    <td>{{ rating.m }}</td>
    <td>{{ rating.w }}</td>
    <td>{{ rating.l }}</td>
    <td>{{ rating.rd }}</td>
    <td>{{ rating.p }}</td>
  </tr>
</template>

<script>
export default {
  name: "GridRating",
  props: ['rating']
}
</script>