import Avatars from "@dicebear/avatars";
import sprites from "@dicebear/avatars-initials-sprites";
import MapPool from "@/constants/MapPool";
import {matchTitles, playerRatingSortRule, scoreboardSortRule, teamRatingSortRule} from "@/constants/InternalStorage";
import moment from "moment";

export function updateCommonData(state, commonData) {
    const avatarGenerator = new Avatars(sprites, {
        radius: 10,
        base64: true,
        width: 150,
        height: 150,
        margin: 10,
        bold: true
    })
    const officialMapPool = MapPool

    // Adding avatars to players
    for (const player of commonData.players)
        player.playerImg = avatarGenerator.create(player.nickname)

    commonData.rating = {common: []}
    for (const matchTitle in matchTitles) {
        commonData.rating[matchTitle] = []
    }

    // Processing teams
    for (const team of commonData.teams) {
        team['players'] = commonData.players.filter(it => it.team === team.id).sort((a, b) => {
            let an = a.nickname.toLowerCase()
            let bn = b.nickname.toLowerCase()
            return an > bn ? 1 : an < bn ? -1 : 0
        })
        commonData.rating.common[team.id] = {ratingKey: `common_${team.id}`, team: team, m: 0, w: 0, l: 0, rd: 0, p: 0}
        commonData.rating.group_stage[team.id] = {
            ratingKey: `group_stage_${team.id}`,
            team: team,
            m: 0,
            w: 0,
            l: 0,
            rd: 0,
            p: 0
        }
    }

    let player_ratings = {};

    // Processing matches
    for (const match of commonData.matches) {
        for (const teamIndex of ['a', 'b']) {
            // Making relation to teams
            match.teams[teamIndex] = commonData.teams.find(it => it.id === match.teams[teamIndex].id) ?? match.teams[teamIndex]
        }

        // Bans and picks processing
        let maps = {bans: {a: [], b: []}, picks: {a: [], b: []}, decider: null, mobilebans: []}
        for (let i = 0, l = match.mappool.ban.a.length; i < l; i++) {
            maps.bans.a.push({...officialMapPool[match.mappool.ban.a[i]], ban: match.teams.a.name})
            maps.bans.b.push({...officialMapPool[match.mappool.ban.b[i]], ban: match.teams.b.name})
            maps.mobilebans.push(
                {...officialMapPool[match.mappool.ban.a[i]], ban: match.teams.a.name},
                {...officialMapPool[match.mappool.ban.b[i]], ban: match.teams.b.name}
            )
        }
        for (let i = 0, l = match.mappool.pick.a.length; i < l; i++) {
            maps.picks.a.push({...officialMapPool[match.mappool.pick.a[i]], pick: match.teams.a.name})
            maps.picks.b.push({...officialMapPool[match.mappool.pick.b[i]], pick: match.teams.b.name})
        }
        maps.decider = {...officialMapPool[match.mappool.pick.org], decider: true}
        match.mappool.bans = maps.bans
        match.mappool.picks = maps.picks
        match.mappool.decider = maps.decider
        match.mappool.mobilebans = maps.mobilebans

        // Adding player info to scoreboard
        for (const map of match.maps) {
            for (const teamIndex of ['a', 'b'])
                for (const player of map.scoreboard[teamIndex]) {
                    player.info = commonData.players.find(it => it.steamid === player.steamid)
                    // Adding player stats to common players rating
                    let prate = player_ratings[player.steamid] ?? {
                        info: player.info,
                        rating: {},
                        steamid: player.steamid
                    }
                    for (const playerKey in player) {
                        if (!Object.prototype.hasOwnProperty.call(player, playerKey) || playerKey === 'steamid' || playerKey === 'info')
                            continue;
                        if (!prate.rating[playerKey])
                            prate.rating[playerKey] = player[playerKey]
                        else
                            prate.rating[playerKey] += player[playerKey]
                    }
                    player_ratings[player.steamid] = prate
                }
            // Sorting scoreboard
            for (const teamIndex of ['a', 'b'])
                map.scoreboard[teamIndex].sort(scoreboardSortRule)
        }


        // Processing score and time for grid
        match.score = {a: 0, b: 0}
        match.datetime = {date: "", time: ""}
        let roundDiffA = 0
        for (const map of match.maps) {
            roundDiffA += map.score.a - map.score.b
            if (map.score.a > map.score.b)
                match.score.a += 1
            else
                match.score.b += 1
        }
        if (match.time >= Date.now() / 1000) {
            match.status = 'upcoming'
        } else if (match.score.a >= match.bestof - 1 || match.score.b >= match.bestof - 1)
            match.status = 'finished'
        else match.status = 'live'
        moment.locale("ru")
        const matchMoment = moment(match.time * 1000)
        const matchdate = matchMoment.format("D MMMM YYYY")
        const matchtime = matchMoment.format("HH:mm")
        if (match.status === 'upcoming') {
            match.datetime.time = matchtime
            match.datetime.date = matchdate
        } else
            match.datetime.date = `${matchdate} ${matchtime}`

        // Calculating ratings
        if (match.status !== 'finished')
            continue;
        const constantPPW = 3
        if (match.title !== 'showmatch') {
            const commonAdata = commonData.rating.common[match.teams.a.id]
            const commonBdata = commonData.rating.common[match.teams.b.id]
            commonData.rating.common[match.teams.a.id] = ratingCalculator(commonAdata, match.teams.a, match.score, constantPPW, roundDiffA)
            commonData.rating.common[match.teams.b.id] = ratingCalculator(commonBdata, match.teams.b, match.score, constantPPW, roundDiffA, true)
        }

        const boundData = commonData.rating[match.title]
        const boundAdata = boundData[match.teams.a.id]
        const boundBdata = boundData[match.teams.b.id]
        boundData[match.teams.a.id] = ratingCalculator(boundAdata, match.teams.a, match.score, constantPPW, roundDiffA, false, match.title)
        boundData[match.teams.b.id] = ratingCalculator(boundBdata, match.teams.b, match.score, constantPPW, roundDiffA, true, match.title)
    }

    commonData.rating.common.sort(teamRatingSortRule)
    commonData.rating.group_stage.sort(teamRatingSortRule)
    player_ratings = Object.values(player_ratings)
    player_ratings.sort(playerRatingSortRule)

    // Writing to state
    state.teams = commonData.teams
    state.players = commonData.players
    state.matches = commonData.matches
    state.rating = commonData.rating
    state.player_rating = player_ratings
}

function ratingCalculator(oldData, team, matchScore, PPW, roundDiff, reverse = false, ratingKey = "common") {
    if (oldData) {
        oldData.m += 1
        if (!reverse) {
            if (matchScore.a >= matchScore.b) {
                oldData.w += 1
                oldData.p += PPW
            } else
                oldData.l += 1

            oldData.rd += roundDiff
            return oldData
        } else {
            if (matchScore.b >= matchScore.a) {
                oldData.w += 1
                oldData.p += PPW
            } else
                oldData.l += 1

            oldData.rd += -roundDiff
            return oldData
        }
    } else {
        if (!reverse) {
            const newData = {ratingKey: `${ratingKey}_${team.id}`, team: team, m: 1, rd: roundDiff, w: 0, l: 0, p: 0}
            if (matchScore.a >= matchScore.b) {
                newData.w = 1
                newData.p = PPW
            } else
                newData.l = 1
            return newData
        } else {
            const newData = {ratingKey: `${ratingKey}_${team.id}`, team: team, m: 1, rd: -roundDiff, w: 0, l: 0, p: 0}
            if (matchScore.b >= matchScore.a) {
                newData.w = 1
                newData.p = PPW
            } else
                newData.l = 1
            return newData
        }
    }
}
