<template>
    <div class="mb-4 matchgrid" :class="click ? 'clickEnabled' : ''" @click.prevent="click?goMatch(match.id):false">
        <div class="col">
            <h4>{{ match.teams.a.name }}</h4>
        </div>
        <div class="col-5 col-md-3">
            <h5 v-if="match.status !== 'upcoming' && (match.score.a || match.score.b)" class="matchscore">
                <span :class="match.score.a > match.score.b ? 'matchscore_more' : 'matchscore_less'">{{ match.score.a }}</span>
                &mdash;
                <span :class="match.score.a > match.score.b ? 'matchscore_less' : 'matchscore_more'">{{ match.score.b }}</span>
            </h5>
            <p class="matchstatus">{{ matchStatuses[match.status] }}</p>
            <h6>{{ match.datetime.time }}</h6>
            <p>{{ match.datetime.date }}</p>
            <p class="matchtitle">{{ matchTitles[match.title] }}</p>
        </div>
        <div class="col">
            <h4>{{ match.teams.b.name }}</h4>
        </div>
    </div>
</template>

<script>
    import {matchStatuses, matchTitles} from "@/constants/InternalStorage";

    export default {
        name: "GridMatch",
        props: ['match', 'click'],
        data: () => ({
            matchTitles, matchStatuses
        }),
        methods: {
            goMatch(matchId) {
                this.$router.push(`/match/${matchId}`)
            }
        }
    }
</script>