import MainPanel from "@/components/panels/MainPanel";
import InfoPanel from "@/components/panels/InfoPanel";
import TeamsPanel from "@/components/panels/TeamsPanel";
import PlayersPanel from "@/components/panels/PlayersPanel";
import LivePanel from "@/components/panels/LivePanel";
import {tournamentName} from "@/constants/InternalStorage";
import MatchPanel from "@/components/panels/MatchPanel";
import RatingPanel from "@/components/panels/RatingPanel";
import MatchesPanel from "@/components/panels/MatchesPanel";

export const routes = [
    {
        path: "/", component: MainPanel
    },
    {
        path: "/info", component: InfoPanel, meta: {
            title: "Информация"
        }
    },
    {
        path: "/matches", component: MatchesPanel, meta: {
            title: "Матчи"
        }
    },
    {
        path: "/teams", component: TeamsPanel, meta: {
            title: "Команды"
        }
    },
    {
        path: "/players", component: PlayersPanel, meta: {
            title: "Игроки"
        }
    },
    {
        path: "/rating", component: RatingPanel, meta: {
            title: "Рейтинг"
        }
    },
    {
        path: "/live", component: LivePanel, meta: {
            title: "Онлайн-трансляция"
        }
    },
    {
        path: "/match/:id", component: MatchPanel, meta: {
            title: "Просмотр матча"
        }
    }
]

export const routeTitleChanger = (Vue, to) => {
    Vue.nextTick(() => {
        if (to.meta.title)
            document.title = `${tournamentName} | ${to.meta.title}`
        else
            document.title = tournamentName
    })
}