<template>
  <tr class="text-center">
    <td class="text-left"><a :href="`//steamcommunity.com/profiles/${steamid}`">{{ info ? info.nickname : steamid }}</a>
    </td>
    <td>{{ rating.kills }}</td>
    <td>{{ rating.deaths }}</td>
    <td>{{ rating.assists }}</td>
    <td>{{ rating.flashes }}</td>
    <td>{{ rating.bombplants }}</td>
    <td>{{ rating.bombdefuses }}</td>
    <td>{{ rating.trades }}</td>
    <td>{{ rating.teamkills }}</td>
    <td :class="rating.kills - rating.deaths >= 0 ? 'text-light-success' : 'text-light-danger'">
      {{ kd }}
    </td>
    <td>{{ (rating.damage / rating.rounds).toPrecision(3) }}</td>
    <td>{{ (rating.kills / rating.rounds).toPrecision(rating.kills / rating.rounds >= 1 ? 3 : 2) }}</td>
    <td>{{ rating.headshots === 0 ? 0 : (rating.headshots / rating.kills * 100).toPrecision(3) }}%</td>
    <td>{{ rating["5kr"] }}</td>
  </tr>
</template>

<script>
export default {
  name: "GridPlayer",
  props: ['info', 'rating', 'steamid', 'kd_divide'],
  computed: {
    kd() {
      if (this.kd_divide)
        return (this.rating.kills / this.rating.deaths).toPrecision(this.rating.kills / this.rating.deaths >= 1 ? 3 : 2)
      else
        return (this.rating.kills - this.rating.deaths >= 0 ? "+" : "") + (this.rating.kills - this.rating.deaths)
    }
  }
}
</script>
