<template>
    <div id="teams">
        <GridMatch v-for="match in matches" :key="match.id" v-bind="{match, click: true}"/>
    </div>
</template>

<script>
    import GridMatch from "@/components/molecules/GridMatch";

    export default {
        name: "MatchesPanel",
        data: () => ({
            matches: []
        }),
        components: {GridMatch},
        created() {
            this.updateMatches(this.$store.state.matches)
            this.unwatch = this.$store.watch((state, getters) => getters.matches, (matches) => {
                this.updateMatches(matches)
            })
        },
        methods: {
            updateMatches(matches) {
                this.matches = matches
            }
        },
        beforeDestroy() {
            this.unwatch();
        }
    }
</script>

